@import url("https://fonts.googleapis.com/css?family=Press+Start+2P");
@import url("https://fonts.googleapis.com/css?family=VT323");

.App {
  text-align: center;
  font-family: "Rockwell", "VT323";
  color: white;

  
}


* {
  box-sizing: border-box;
}

body {
  overflow: hidden;
}

.overlay {
  opacity: 0.9;
  width: 100%;
  height: 100%;
  z-index: 10;
  top: 0;
  left: 0;
  position: fixed;
}

.moving-background {
  z-index: -2;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: url("./assets/background/NFT.png");

  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: 50% 50%;
}

/* background-image: url("./assets/background/NFT.png"); */




/* For mobile phones: */





@media only screen and (min-width: 600px) {
  /* For tablets: */
  .col-s-1 {width: 8.33%;}
  .col-s-2 {width: 16.66%;}
  .col-s-3 {width: 2000%;}
  .col-s-4 {width: 1%;}
  .col-s-5 {width: 41.66%;}
  .col-s-6 {width: 50%;}
  .col-s-7 {width: 58.33%;}
  .col-s-8 {width: 66.66%;}
  .col-s-9 {width: 75%;}
  .col-s-10 {width: 83.33%;}
  .col-s-11 {width: 91.66%;}
  .col-s-12 {width: 100%;}
}

@media only screen and (max-width: 700px) {
  /* For phone : */

  .col-1 {
    
   
 

  }
 
  .col-2 {width: 16.66%;}
  .col-3 {width: 200%;}
  .col-4 {
   width: 100px;
   position: fixed;
   inset: 10% 0% 0% 2%;
   size: 1px;
   padding: 1px;
   
  
   

  
  }
   
 
   


  .col-6 {
    width: 100%;
    height: 100%;
  
  }
  .Facebook {
    position: fixed;
    inset: 10% 0 0 26%;
   
  }
  .Twitter {
    position: fixed;
    inset: 20% 0 0 26%;
    
  }
  .Instagram {
    position: fixed;
    inset: 30% 0 0 26%;
    }
  .Button {
    position: fixed;
    inset: 0 0 0 80%;
    
  }
  
  .col-11 {width: 91.66%;}
  .col-12 {width: 100%;}
}















